 @import \"@tailwindcss/forms\"; */\n\n/* Custom scrollbar styling */\n/* For Webkit browsers (Chrome, Safari, Edge) */\n::-webkit-scrollbar {\n  width: 8px;\n  height: 8px;\n}\n\n::-webkit-scrollbar-track {\n  background: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  background-color: rgba(156, 163, 175, 0.5); /* Gray with transparency */\n  border-radius: 6px;\n  border: 2px solid transparent;\n  background-clip: content-box;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background-color: rgba(156, 163, 175, 0.7);\n}\n\n/* For Firefox */\n* {\n  scrollbar-width: thin;\n  scrollbar-color: rgba(156, 163, 175, 0.5) transparent;\n}\n\n/* Dark mode scrollbars */\n.dark::-webkit-scrollbar-thumb {\n  background-color: rgba(75, 85, 99, 0.5);\n}\n\n.dark::-webkit-scrollbar-thumb:hover {\n  background-color: rgba(75, 85, 99, 0.7);\n}\n\n.dark * {\n  scrollbar-color: rgba(75, 85, 99, 0.5) transparent;\n}\n\nhtml, body {\n  margin: 0;\n  padding: 0;\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\n    sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  height: 100vh;\n  width: 100vw;\n  overflow: hidden;\n}\n\n#root {\n  height: 100vh;\n  display: flex;\n  flex-direction: column;\n}\n\n/* Ensure the application container takes full height */\nmain {\n  flex: 1;\n  min-height: 0;\n  display: flex;\n  flex-direction: column;\n}\n\n/* Dark mode styles */\n.dark body {\n  /* @apply bg-surface-900 text-surface-100; */\n  background-color: var(--color-surface-900);\n  color: var(--color-surface-100);\n}\n\n.dark .markdown-preview {\n  /* @apply bg-surface-800 text-surface-100 border-surface-700; */\n  background-color: var(--color-surface-800);\n  color: var(--color-surface-100);\n  border-color: var(--color-surface-700);\n}\n\n.dark .markdown-preview pre {\n  /* @apply bg-surface-900; */\n  background-color: var(--color-surface-900);\n}\n\n/* Responsive styles */\n@media (max-width: 768px) {\n  /* Adjust gutters */\n  .gutter {\n    background-color: rgba(120, 120, 120, 0.2);\n  }\n  \n  .gutter.gutter-horizontal {\n    cursor: col-resize;\n    height: 100%;\n    width: 8px !important;\n  }\n  \n  .gutter.gutter-vertical {\n    cursor: row-resize;\n    width: 100%;\n    height: 8px !important;\n  }\n  \n  /* Make sure the mobile preview is scrollable */\n  .preview-container {\n    max-height: 100%;\n    overflow-y: auto;\n  }\n  \n  /* Ensure the mobile floating buttons are visible */\n  .fixed {\n    z-index: 50;\n  }\n}\n\n/* Override react-split styles for mobile */\n@media (max-width: 768px) {\n  .responsive-split {\n    display: flex !important;\n    flex-direction: column !important;\n  }\n  \n  .responsive-split > .gutter {\n    width: 100% !important;\n    height: 8px !important;\n    cursor: row-resize !important;\n  }\n  \n  .responsive-split-vertical {\n    flex-direction: column !important;\n  }\n  \n  .responsive-split-vertical > .gutter {\n    width: 100% !important;\n    height: 8px !important;\n  }\n}\n\n/* Ensure the mobile sidebar is scrollable */\n.sidebar-mobile {\n  overflow-y: auto !important;\n  max-height: 100vh;\n}\n\n/* Accessibility styles */\n.sr-only {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  white-space: nowrap;\n  border-width: 0;\n}\n\n/* High contrast focus styles */\n:focus-visible {\n  outline: 2px solid #0ea5e9;\n  outline-offset: 2px;\n}\n\n/* Improves readability in high contrast mode */\n@media (forced-colors: active) {\n  * {\n    border-color: currentColor;\n  }\n  \n  button, \n  a, \n  .gutter {\n    forced-color-adjust: none;\n  }\n  \n  /* Ensure focus outlines are visible in high contrast mode */\n  :focus {\n    outline: 2px solid transparent;\n    outline-offset: 2px;\n  }\n}\n\n/* Reduce motion for users who prefer reduced motion */\n@media (prefers-reduced-motion: reduce) {\n  *, *::before, *::after {\n    animation-duration: 0.01ms !important;\n    animation-iteration-count: 1 !important;\n    transition-duration: 0.01ms !important;\n    scroll-behavior: auto !important;\n  }\n}\n\n/* Improved keyboard navigation */\n[tabindex]:not([tabindex=\"-1\"]):focus,\na:focus,\nbutton:focus,\ninput:focus,\nselect:focus,\ntextarea:focus {\n  outline: 2px solid #0ea5e9;\n  outline-offset: 2px;\n}\n\n/* Skip to content link when focused */\n.skip-to-content {\n  background: #0ea5e9;\n  color: white;\n  font-weight: bold;\n  left: 50%;\n  padding: 8px;\n  position: absolute;\n  transform: translateY(-100%);\n  transition: transform 0.3s;\n  z-index: 100;\n}\n\n.skip-to-content:focus {\n  transform: translateY(0%);\n}\n\n/* Focus trap for modals - prevent background interaction when modal is open */\nbody.modal-open {\n  overflow: hidden;\n} "],"sourceRoot":""}]);
